
import React from "react";
import './LayoutStyles.css';

function Etiqueta() {
    return (
        <>
            <div className='col-12 etiqueta'>

                <div className='etiqueta-texto'>
                    <div className='title'>
                        <h2>Etiqueta</h2>
                        <h4>Ven como mejor te sientas!</h4>
                        <p>
                            Queremos que sea una fiesta informal en la que cada cual vaya como quiera.

                            Nosotros nos pondremos bien guapos, con traje y corbata para la ceremonia,
                            aunque probablemente nos cambiemos para bailar despus ms cmodamente.

                            As que sintete libre.

                            Eso s, ten en cuenta que en Rascafra, al caer el sol, la temperatura baja casi siempre,
                            incluso en junio. Es recomendable traerse algo por si refresca.

                        </p>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Etiqueta;